
const Noticia = (props) => {

     const {description, title, url, urlToImage, content, source} = props.noticia;
      
     let imagen = '';

     if ( urlToImage !== ''){
          imagen = <img src={urlToImage} alt={title} className="card-img-top" />
     }


     return(

        <div className="col-md-6 col-12 mb-4">
             <div className="card">
                  <div className="card-body">
                       <h4 className="card-title"> {title}</h4>
                  </div>
                <div className="card-image">
                     
                     {imagen} 
                     <p className="blockquote m-3">{description}</p>
                     <br/>
                      <button type="button" className="btn btn-warning btn-block"><a href={url} className="font-weight-bold text-white" target="_blank">See News</a></button>
                      <p className="text-center mt-3">Source: {source.name}</p>
                </div>

             </div>
        </div>
     );


}

export default Noticia;